@font-face {
  font-family: "mainText";
  src: url("Century\ Gothic.ttf");
}

body{
  background-color: white;
  font-family: "mainText";
}

.phoneText{
  display: block;
}
.slideshow-container{
  display: none;
  max-width: 100vw;
  overflow: hidden;
}

.linkContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.linkBox{
    width: 100%;
    height: 30%;
    font-size: 20px;
    padding: 25px 15px 25px 15px;
    border: 1px solid black;
    border-radius: 10px;
}

.highlight{
  background-color: lightgray;
}

#recordButton{
  background-color: green;
  padding: 10px 20px 10px 20px;
  margin: 1em;
  color: white;
  border-radius: 10px;
  cursor: pointer;
}

.flex-start{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  width: 100%;
  height: 100%;
}

.flex-row{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    width: 100%;
}

.fa-arrow-left {
    -webkit-animation: bounceLeft 2s infinite;
    animation: bounceLeft 2s infinite;
    width: 100%;
  }

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

/* Bouncing animation */
@-webkit-keyframes bounceLeft {
    0%,
    20%,
    50%,
    80%,
    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
    }
    40% {
      -webkit-transform: translateX(30px);
      transform: translateX(30px);
    }
    60% {
      -webkit-transform: translateX(15px);
      transform: translateX(15px);
    }
  }
  @-moz-keyframes bounceLeft {
    0%,
    20%,
    50%,
    80%,
    100% {
      transform: translateX(0);
    }
    40% {
      transform: translateX(30px);
    }
    60% {
      transform: translateX(15px);
    }
  }
  @keyframes bounceLeft {
    0%,
    20%,
    50%,
    80%,
    100% {
      -ms-transform: translateX(0);
      transform: translateX(0);
    }
    40% {
      -ms-transform: translateX(30px);
      transform: translateX(30px);
    }
    60% {
      -ms-transform: translateX(15px);
      transform: translateX(15px);
    }
  }

@media only screen and (min-width:641px) {
  /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */ 
  .fa-arrow-left{
      font-size: 1.2em;
  }
  .slideshow-container{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      z-index: 2;
      overflow: visible;
  }

  .phoneText{
      font-size: 1em;
      display: none;
      text-align: center;
  }

  .title{
      font-size: 2em;
      width: 100%;
      flex-basis: 15%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      position: relative;
      padding: 10px 0 10px 0;
      text-align: center;
      text-decoration: overline underline gray;
      margin: 1em;
      color: rgb(78,90,131);
  }  

  .slide-container{
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      flex-basis: 85%;
      width: 100%;
  }

  .slide{
      display: none;
      flex-basis: 80%;
      width: 20em;
      height: 20em;
      justify-content: center;
      align-items: center;
      flex-direction: row;
      position: relative;
  }

  .image-container{
    width: 100%;
    height: 240px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    position: relative;
  }

  .image{
      width: 15em;
      height: auto;
      margin-right: 10px;
  }

  .avatar-container{
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      justify-self: flex-start;
      align-self: flex-start;
      width: 100%;
  }

  .avatar{
      width: 3em;
      height: auto;
      cursor: pointer;
  }

  .input-container{
      flex-basis: 40%;
      width: 5em;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
  }

  .input-box{
      text-align: center;
      padding: .5em;
      margin: .5em;
      width: 5em;
      border: 0;
      border-bottom: .1em solid darkgray;
      outline: none;
      background-color: transparent;
      font-size: 1em;
  }

  .begin-button{
      cursor: pointer;
      padding: .2em 1em .2em 1em;
      margin: .3em;
      font-size: 1em;
      text-align: center;
      border: .1em solid darkgray;
      background-color: transparent;
  }

  .text-container{
      flex-basis: 40%;
      width: 5em;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      text-align: start;
  }

  p{
    font-size: 1em;
    margin: 0;
  }

  .speech{
      width: 15em;
      background: rgba(250, 235, 215);
      padding: 1em;
      text-align: start;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      font-weight: 700;
      font-size: 1em;
      border-radius: .8em;
      color: black;
      display: none;
      position: absolute;
      transform: translate(20em, -1.5em);
      z-index: 10;
  }

  .speech2{
    width: 10em;
    justify-self: center;
    margin: 5px;
    align-self: center;
    background: white;
    padding: 5px;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-weight: 700;
    font-size: 1em;
    border-radius: .8em;
    color: black;
    display: none;
    z-index: 10;
  }
    
  .sb1:before {
      content: "";
      width: 0px;
      height: 0px;
      position: absolute;
      border-left: 10px solid transparent;
      border-right: 10px solid rgba(250, 235, 215);
      border-top: 10px solid rgba(250, 235, 215);
      border-bottom: 10px solid transparent;
      left: -19px;
      top: 6px;
  }

  .sb2:before {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 10px solid #adbcec;
    border-right: 10px solid transparent;
    border-top: 10px solid transparent;
    border-bottom: 10px solid #adbcec;
    left: 0px;
    top: -10px;
  }

  .sb3:before {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 10px solid transparent;
    border-right: 10px solid #adbcec;
    border-top: 10px solid transparent;
    border-bottom: 10px solid #adbcec;
    right: 0px;
    top: -10px;
  }

  .success-checkmark:after {
      content: '✔';
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 1em; 
      height: 1em;
      text-align: center;
      border: 1px solid #aaa;
      background: greenyellow;
      border-radius: 50%;
      box-shadow: inset 0 1px 3px rgba(0,0,0,.3)
  }

  .arrow-container{
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .arrow-container{
      flex-basis: 10%;
      width: 5em;
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .next {
      cursor: pointer;
      padding: .2em;
      color: rgb(78,90,131);
      font-weight: bold;
      font-size: 2em;
      transition: 0.6s ease;
      border-radius: 0 3px 3px 0;
      user-select: none;
      margin: .5em;
      right: 0%;
      display: none;
  }
  .next:hover {
      background-color: #e0e0e0;
  }

  .pageNum-container{
      width: 100%;
      margin: 1.5em;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
  }

  .question-prompt-container{
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: start;
      flex-basis: 50%;
  }

  .audio-controls-container{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex-basis: 50%;
  }

  .audio-button{
      width: 3em;
      height: 3em;
      border-radius: 1vw;
      cursor: pointer;
  }

  .question-container{
      display: flex;
      flex-basis: 100%;
      width: 100%;
      flex-direction: column;
      justify-content: center;
  }

  .question-title{
      font-size: 1.6em;
      display: flex;
      justify-content: center;
      align-items: center;
  }

  .question{
      font-size: 1.2em;
  }

  .question-option:hover{
    cursor: pointer;
  }

  .popup-image{
    width: 8em;
    margin: 5px;
    height: auto;
  }
}
@media only screen and (min-width:641px) and (orientation: landscape)  { 
  /* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */
  .fa-arrow-left{
    font-size: 1.2em;
}

  .phoneText{
      font-size: 1em;
  }

  .title{
      font-size: 2.5em;
      padding: 30px;
      transform: translateY(0);
      margin: 0;
  }  

  .slide{
      width: 30em;
      height: auto;
  }

  .image-container{
      width: 100%;
      height: 280px;
  }

  .image{
      width: 17em;
  }

  .avatar{
      width: 3em;
  }

  .input-container{
      flex-basis: 40%;
      width: 10em;
  }

  .input-box{
      padding: .5em;
      margin: .5em;
      width: 8em;
      font-size: 1em;
  }

  .begin-button{
      padding: .3em 1em .3em 1em;
      margin: .3em;
      font-size: 1em;
      border: .1em solid darkgray;
  }

  .text-container{
      flex-basis: 40%;
      width: 10em;
      justify-content: flex-start;
      align-items: flex-start;
  }

  p{
      font-size: 1.2em;
  }

  .speech{
      width: 16em;
      padding: 1em;
      font-weight: 700;
      font-size: 1em;
      transform: translate(22em, -5em);
  }

  .speech2{
    width: 12em;
    padding: 1em;
    font-weight: 700;
    font-size: 1em;
  }
    
  .arrow-container{
      flex-basis: 10%;
      width: 5em;
  }
  .next {
      padding: .2em;
      font-size: 2em;
      border-radius: 0 3px 3px 0;
      margin: .5em;
  }

  .question-title{
    font-size: 1.8em;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .question{
      font-size: 1.3em;
  }

  .popup-image{
    width: 8em;
  }

}
